import React from 'react'
import Link from '../Link'
import Tag from '../Tag'

import './Sidebar.css'

const Section = (props) => (
  <section className="section">
    <h5 className="section__title">
      <Link to={props.to || ''}>{props.title}</Link>
    </h5>
    <div className="section__body">
      {props.children}
    </div>
  </section>
)

const Sidebar = () => {
  return (
    <div>
      <Section
        title="FEATURED TAGS"
        to="/tags"
      >
        <div className="featured-tags">
          <Tag to="/tags#前端">前端</Tag>
        </div>
      </Section>
      <Section
        title="ABOUT ME"
        to="/about"
      >
        <div className="short-about">
          <Link to="/about"><img className="short-about__avatar rounded" src="https://avatars0.githubusercontent.com/u/8622362?v=3&amp;s=460" /></Link>
          <p className="short-about__intro">O ever youthful, O ever weeping.</p>
          <ul className="styles.sns list-inline">
            <li className="list-inline-item">
              <a className="sns__link" target="_blank" href="https://github.com/sqrthree">
                <span className="fa-stack fa-lg">
                  <i className="fa fa-circle fa-stack-2x"></i>
                  <i className="fa fa-github fa-stack-1x fa-inverse"></i>
                </span>
              </a>
            </li>
            <li className="list-inline-item">
              <a className="sns__link" target="_blank" href="https://twitter.com/sqrtthree">
                <span className="fa-stack fa-lg">
                  <i className="fa fa-circle fa-stack-2x"></i>
                  <i className="fa fa-twitter fa-stack-1x fa-inverse"></i>
                </span>
              </a>
            </li>
            <li className="list-inline-item">
              <a className="sns__link" target="_blank" href="http://weibo.com/sqrtthree">
                <span className="fa-stack fa-lg">
                  <i className="fa fa-circle fa-stack-2x"></i>
                  <i className="fa fa-weibo fa-stack-1x fa-inverse"></i>
                </span>
              </a>
            </li>
            <li className="list-inline-item">
              <a className="sns__link" target="_blank" href="https://www.zhihu.com/people/sqrtthree">
                <span className="fa-stack fa-lg">
                  <i className="fa fa-circle fa-stack-2x"></i>
                  <i className="fa  fa-stack-1x fa-inverse">知</i>
                </span>
              </a>
            </li>
            <li className="list-inline-item">
              <a className="sns__link" target="_blank" href="mailto:sqrtthree@foxmail.com">
                <span className="fa-stack fa-lg">
                  <i className="fa fa-circle fa-stack-2x"></i>
                  <i className="fa fa-envelope-o fa-stack-1x fa-inverse"></i>
                </span>
              </a>
            </li>
          </ul>
        </div>
      </Section>
      <Section title="FRIENDS">
        <ul className="friend-links list-inline">
          <li className="list-inline-item"><a className="friend-links__link" href="https://github.com/sqrthree">GitHub</a></li>
          <li className="list-inline-item"><a className="friend-links__link" href="https://zhuanlan.zhihu.com/FETalk">FETalk</a></li>
          <li className="list-inline-item"><a className="friend-links__link" href="http://blog.sqrtthree.com/rss.xml">RSS</a></li>
        </ul>
      </Section>
    </div>
  )
}

export default Sidebar



